.about-me {
    padding: 110px 70px 100px 70px;
}

.about-me__heading {
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: -0.04em;
    border-bottom: 1px solid #DADADA;
    color: #FFFFFF;
    height: 50px;
    margin: 0 0 66px 0;
}

.about-me__description {
    display: flex;
    justify-content: space-between;
}

.about-me__description-title {
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 58px;
    letter-spacing: -0.04em;
    color: #FFFFFF;
    margin: 0 0 18px 0;
}

.about-me__description-subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    color: #FFFFFF;
    margin: 0 0 26px 0;
}

.about-me__description-paragraph {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 0 0 80px 0;
    max-width: 600px;
    min-height: 108px;
}

.about-me__description-link {
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    margin-right: 20px;
    transition: opacity 0.3s;
}

.about-me__description-link:hover {
    opacity: 0.7;
}

.about-me__description-photo {
    border-radius: 10px;
    width: 270px;
    height: 327px;
}

@media(max-width: 992px) {
    .about-me {
        padding: 70px 14px;
    }

    .about-me__heading {
        height: 47px;
    }

    .about-me__description-title {
        font-size: 40px;
        line-height: 40px;
        margin: 0 0 16px 0;
    }

    .about-me__description-subtitle {
        font-size: 12px;
        line-height: 18px;
        margin: 0 0 20px 0;
    }

    .about-me__description-paragraph {
        font-size: 12px;
        line-height: 18px;
        margin: 0 0 87px 0;
        max-width: 364px;
    }

    .about-me__description-photo {
        width: 255px;
        height: 307px;
    }
}

@media(max-width: 720px) {
    .about-me {
        padding: 70px 14px;
    }

    .about-me__heading {
        font-size: 18px;
        line-height: 22px;
        height: 49px;
        margin: 0 0 60px 0;
    }

    .about-me__description {
        display: flex;
        flex-direction: column;
    }

    .about-me__description-photo {
        order: -1;
        width: 292px;
        height: 352px;
        margin-bottom: 40px;
    }

    .about-me__description-title {
        font-size: 30px;
        line-height: 36px;
        margin-bottom: 20px;
    }

    .about-me__description-subtitle {
        font-size: 11px;
        line-height: 16px;
        margin-bottom: 20px;
    }

    .about-me__description-paragraph {
        font-size: 11px;
        line-height: 16px;
        margin-bottom: 40px;
        min-height: 112px;
    }
}